export default {
	data() {
		return {
			number:0,
			height:320
		}
	},
	methods:{
		inputContent(e){
			let val = e.mp.detail.value;
			this.content = e.mp.detail.value;
			this.number = val.length;
		},
		linechange(e){	//行数变化
			let lineCount = e.mp.detail.lineCount;
			let num = (lineCount - 1) * 30
			this.height = 320 - num
			console.log(num,this.height);
		},
		close(){
			this.$emit("close");
		},
		submit(){
			console.log('提交')
		}
	}
}
